// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-midnight-express:              #011140;
$color-picton-blue:                   #6796CA;
$color-down:                          #999791;

// Gradient

// $main-gradient:                   conic-gradient(from -3deg at 43.93% 0.00%, #FBE308 1.8750000558793545deg, #0AD3FF 114.37500357627869deg, rgba(49, 220, 97, 0.92) 198.74999284744263deg, #0085FF 286.875deg);
$main-gradient:                      conic-gradient(from -3deg at 43.93% 0.00%, #FBE308 1.8750000558793545deg, #0AD3FF 114.37500357627869deg, rgba(49, 220, 97, 0.92) 250.74999284744263deg, #0085FF 266.875deg);


// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        20px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-montserrat:                         "Montserrat", "Arial", sans-serif;


// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-500:                            500px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
